<template>
  <div class="add-box">
    <div class="edit">
    </div>
    <div class="inner">
      <div class="type">
        &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;类型
        <select @change="hideIndustry" v-model="article.type">
          <option value="">--请选择--</option>
          <option v-for="(x, y) in types" :value="y">{{x}}</option>
        </select>
        <select v-if="showIndustry" v-model="article.industry">
          <option value="">--请选择--</option>
          <option v-for="(x, y) in industry" :value="y">{{x}}</option>
        </select>
      </div>
      <div class="title">
        标题名称
        <input v-model="article.title" type="text">
      </div>
      <div class="url">
        跳转链接
        <input v-model="article.url" type="text">
      </div>
      <div class="explain">
        <span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;说明&nbsp;</span>
        <!-- 加载编辑器的容器 -->
        <quill-editor
          class="quill-editor"
          v-model="article.content"
          ref="myQuillEditor"
          >
        </quill-editor>
      </div>
      <div class="img-upload">
        &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;配图
        <label for="img-upload" class="btn btn-primary">选择文件</label>
        <input @change="imgChoose" id="img-upload" type="file" accept="image/*">
      </div>
      <div>
        <img :src="article.img" @click="zoomImg" :class="{'zoom-in':!zoom,'preview':zoom}">
      </div>
      <table>
        <tr>
          <th>图片名</th>
          <th>文件大小</th>
          <th>进度</th>
          <th>操作</th>
          <th>操作</th>
        </tr>
        <tr>
          <td>{{imgName}}</td>
          <td>{{imgSize}}</td>
          <td>
            <div class="range-value">{{rangeValue}}</div>
            <input  ref="range" class="range" id="range" type="range" min="0" max="100" step="1" disabled>
          </td>
          <td></td>
          <td>
            <button @click="imgUpload" :disabled="dis">上传</button>
            <button @click="imgDelete" :disabled="dis2">删除</button>
          </td>
        </tr>
      </table>
      <button @click="onOffLine(2)" :disabled="disOnOff">立即上线</button>
      <button @click="onOffLine(1)" :disabled="disOnOff">存为草稿</button>
      <button @click="cancel">取消</button>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'add',
    data () {
      return {
        types: window._const.types,
        industry: window._const.industry,
        zoom: true, // 图片缩放
        dis: true,  // 上传按钮禁用
        dis2: false, //上传图片时，禁用删除按钮
        imgName: '',
        imgSize: '',
        imgSrc: '',
        rangeValue: '', // 上传图片时的进度条
        article: {
          type:'',
          industry: ''
        },
        showIndustry: false,
        forbidden: false
      }
    },
    methods:{
      cancel:function () {
        history.go(-1)
      },
      // 行业大图显示和隐藏
      hideIndustry:function () {
        if (this.article.type == 3){
          this.showIndustry = !this.showIndustry
        }
        else {
          this.showIndustry = false
          this.article.industry = ''
        }
      },
      // 预览图缩放
      zoomImg:function () {
        this.zoom = !this.zoom
      },
      // 选择图片并显示名称和大小
      imgChoose:function (e) {
        this.dis = false  // 启用上传按钮
        // let reader = new FileReader();
        let file = e.target.files[0]  // 获取input中图片的信息
        this.imgName = file.name
        this.imgSize = ((file.size)/(1024*1024)).toFixed(2)+'mb'
        this.imgSrc = new FormData(); // 因为接口是MultipartFile，所以需要用formdata传
        this.imgSrc.append('file', file); // 将图片信息存储

        // reader.readAsDataURL(file); //使用FileReader转换图片
        // reader.onload = () => { //转换完成后的操作
        // }
      },
      // 图片上传
      imgUpload:function () {
        // 点击上传后禁用按钮，防止重复上传
        this.dis = true
        this.dis2 = true
        this.forbidden = true
        this.rangeValue = 80; //进度条value
          this.$refs.range.style.backgroundSize = this.rangeValue+"% 100%";  //进度条
          this.$axios({
            method: 'post',
            url: 'api/a/u/img/task',
            data: this.imgSrc
          })
            .then((res) => {
              if (res.data.code === 0){ // 上传成功
                this.rangeValue = 100; //进度条value
                this.$refs.range.style.backgroundSize = this.rangeValue+"% 100%";  //进度条
                this.article.img = res.data.data.url  // 改变当前预览的图片
                console.log(res);
                this.dis2 = false
                this.forbidden = false
              }
            })
      },
      // 删除图片
      imgDelete:function () {
        this.$confirm('警告：删除后将不再有图片显示，是否删除？','提示',{
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
          .then(() => {
            this.rangeValue = 0
            this.$refs.range.style.backgroundSize = this.rangeValue+"% 100%";  //进度条
            this.article.img = ''
            this.imgSize = ''
            this.imgName = ''
            this.dis = true
          })
          .catch(() => {})
      },
      // 上线或存为草稿
      onOffLine(onOff){
        this.article.status = onOff //根据html按钮的参数进行上线或草稿
        // 新增
        if (this.$route.query.id == ''){
          this.$axios({
            method:'post',
            url:'api/a/u/article',
            params: this.article
          })
            .then(() => this.$message({
              type: 'info',
              message: '操作已成功'
            }))
        }
        // 编辑
        else {
          console.log(this.article);
          this.$axios({
            method: 'put',
            url: 'api/a/u/article/'+this.$route.query.id,
            params: this.article
          })
            .then(() => this.$message({
              type: 'info',
              message: '操作已成功'
            }))
        }
      },
    },
    // 获取数据
    beforeCreate () {
      if (this.$route.query.id == undefined){
        this.$route.query.id = ''
      }
      else {
        this.$axios({
          method:'get',
          url:'api/a/article/'+this.$route.query.id
        })
          .then((res) => {
            console.log(res);
            this.article = res.data.data.article
          })
      }
    },
    computed:{
      disOnOff (){
        // 当类型 标题 链接已填写时，提交可用
        if (this.article.type !== '' && this.article.title != '' && this.article.url != ''){
          // 当类型为行业大图时，industry为必填
          if (this.article.type == 3 && this.article.industry == '' || this.forbidden == true){
            return true
          }
          else {
            return false
          }
        }
        else {
          return true
        }
      } // 上线和草稿按钮禁用，必须填写完必填项
    }
  }
</script>

<style>

  .add-box > .edit {
    background-color: #fbfbfb;
    border: 1px solid #cccccc; }
  .add-box > .inner {
    padding: 50px;
    border: 1px solid #cccccc;
    border-top: none; }
  .add-box > .inner > .title > input, .add-box > .inner .url > input {
    width: 60%; }
  .add-box > .inner > .img-upload > input {
    display: none; }
  .add-box > .inner > div > .preview {
    padding-bottom: 10px;
    max-width: 200px;
    transition: all 0.2s;
    cursor: zoom-in;
  }
  .add-box > .inner > div > .zoom-in {
    padding-bottom: 10px;
    transition: all 0.2s;
    max-width: 100%;
    cursor: zoom-out;
  }
  .add-box > .inner > .explain {
    display: flex; }
  .add-box > .inner > .explain > span {
    word-break: keep-all; }
  .add-box > .inner > div {
    padding-bottom: 20px; }
  .add-box > .inner > table {
    margin-bottom: 20px; }
  .add-box > .inner > table th, .add-box > .inner > table td {
    padding: 10px 20px;
    text-align: center; }
  .add-box > .inner > table td {
    position: relative;
    vertical-align: middle; }
  .add-box > .inner > table td > .range {
    padding: 0;
    outline: none;
    -webkit-appearance: none;
    width: 100%;
    border: 1px solid #cccccc;
    border-radius: 10px;
    background: -webkit-linear-gradient(#0930a6, #0e4aff, #07298c) no-repeat, #c9c9c9;
    background-size: 0 100%;
    height: 23px;
    transition: all linear 0.5s; }
  .add-box > .inner > table td > .range::-webkit-slider-thumb {
    display: none; }
  .add-box > .inner > table td > .range-value {
    position: absolute;
    left: calc(50% - 20px);
    top: calc(50% - 12px);
    width: 40px;
    height: 22px;
    line-height: 22px;
    color: white; }
  .add-box > .inner table, .add-box > .inner th, .add-box > .inner td {
    border-collapse: collapse;
    border: 1px solid #cccccc; }
  .quill-editor {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    margin-bottom: 80px;
  }
</style>
